    font-family: "Roboto Condensed", "Arial", sans-serif;
    font-weight: 700;
    font-style: normal;
    font-size: 25px;
    line-height: 29px;
    letter-spacing: 0;
    text-align: left;
    color: #ffffff;
  }
}

@media (min-width: 768px) {
  .header__slogan-text {
    font-family: "Roboto", "Arial", sans-serif;
    font-weight: 500;
    font-style: normal;
    font-size: 12px;
    line-height: 14px;
    font-weight: 500;
    letter-spacing: 0;
    text-align: left;
    color: rgba(255, 255, 255, 0.7);
